export class User {
  constructor(information) {
    // Le constructeur de la classe User est appel√© lorsqu'une nouvelle instance est cr√©√©e.
    // Il prend les informations d'un utilisateur en tant que param√®tre.

    // Stocke les informations pass√©es en param√®tre dans la propri√©t√© "information".
    this.information = information;

    // Initialise l'√©tat de pr√©sence √† "false".
    this.present = false;

    // Appelle la m√©thode render() pour g√©n√©rer l'√©l√©ment utilisateur.
    this.element = this.render();

    // Ajoute un √©couteur d'√©v√©nement "click" sur l'√©l√©ment pour g√©rer le changement d'√©tat de pr√©sence.
    this.element.addEventListener("click", this.togglePresence.bind(this));
  }

  updateCounter() {
    // S√©lectionnez l'√©l√©ment de compteur dans le DOM
    const counterElement = document.querySelector(".counter");

    // Comptez le nombre d'utilisateurs pr√©sents en parcourant les √©l√©ments d'utilisateurs
    const presentUsers = document.querySelectorAll(
      ".user[data-present='true']"
    ).length;

    // Mettez √† jour le texte du compteur avec le nombre d'utilisateurs pr√©sents
    counterElement.textContent = `${presentUsers}/20 people are here`;
  }

  togglePresence() {
    // Cette m√©thode est appel√©e lorsque l'√©l√©ment utilisateur est cliqu√©.
    // Elle inverse l'√©tat de pr√©sence.
    this.present = !this.present;

    // Appelle la m√©thode updateElementStyle() pour mettre √† jour les styles en fonction de l'√©tat de pr√©sence.
    this.updateElementStyle();

    // Mettre √† jour l'attribut data-present en fonction de l'√©tat de pr√©sence
    this.element.setAttribute("data-present", this.present);

    this.updateCounter(); // Appel de la mise √† jour du compteur
  }

  updateElementStyle() {
    // Cette m√©thode met √† jour les styles de l'√©l√©ment en fonction de l'√©tat de pr√©sence.

    if (this.present) {
      // Si l'utilisateur est pr√©sent, les styles sont mis √† jour pour afficher la couleur de pr√©sence.
      this.element.style.backgroundColor = "#06d6a0";
      this.element.style.color = "#ffffff";
      this.element.querySelector(".user--info p").style.color = "#ffffff";
    } else {
      // Si l'utilisateur n'est pas pr√©sent, les styles sont r√©initialis√©s aux valeurs par d√©faut.
      this.element.style.backgroundColor = "#ffffff";
      this.element.style.color = "#010123";
      this.element.querySelector(".user--info p").style.color = "#666666";
    }
  }

  render() {
    // Cette m√©thode g√©n√®re et retourne l'√©l√©ment utilisateur.

    // Cr√©e un √©l√©ment "section" pour le conteneur de l'utilisateur.
    const containerElement = document.createElement("section");
    containerElement.classList.add("user"); // Ajoute la classe CSS "user" au conteneur.

    // G√©n√®re le code HTML pour l'√©l√©ment utilisateur en utilisant les informations de l'utilisateur.
    const childHTML = `
        <img src="${this.information.image}">
        <div class="user--info">
          <h1>${this.information.name}</h1>
          <p>${this.information.age} years old</p>
          <p>${this.information.location}</p>
          <a href="mailto:${this.information.email}">
            <span class="mail">‚úâÔ∏è</span>
          </a>
        </div>
      `;

    // Ins√®re le code HTML g√©n√©r√© dans le conteneur de l'utilisateur.
    containerElement.insertAdjacentHTML("afterbegin", childHTML);

    // D√©finit l'attribut de donn√©es "present" √† "false" par d√©faut pour le toggle.
    containerElement.dataset.present = "false";

    // D√©finit les styles par d√©faut pour le conteneur de l'utilisateur.
    containerElement.style.backgroundColor = "#ffffff";
    containerElement.style.color = "#010123";

    // Retourne le conteneur de l'utilisateur.
    return containerElement;
  }
}
